/**
 *  ...
 */
export class Snippet {
  get src() {
    return this.source;
  }

  constructor(private source: string) {}

  /**
   *  ...
   */
  occurrences(pattern: RegExp) {
    return this.source.match(pattern)?.length ?? 0;
  }

  /**
   *  ...
   */
  contains(pattern: string | RegExp) {
    return pattern instanceof RegExp
      ? pattern.test(this.source)
      : this.source.includes(pattern);
  }

  /**
   *  ...
   */
  replace(searchValue: string | RegExp, replaceValue: string | Snippet) {
    replaceValue =
      typeof replaceValue === 'string' ? replaceValue : replaceValue.src;

    return new Snippet(this.source.replace(searchValue, replaceValue));
  }

  /**
   *  ...
   */
  sub(searchValue: string | RegExp, replaceValue: string | Snippet, count = 0) {
    replaceValue =
      typeof replaceValue === 'string' ? replaceValue : replaceValue.src;

    return new Snippet(this.source.replace(searchValue, replaceValue));
  }

  strip() {
    return new Snippet(this.source.trim());
  }

  /**
   *  ...
   */
  clone() {
    return new Snippet(this.source);
  }
}
